import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:"",
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
        }


        $.ajax({
            url:"/api/user/signup",

            type:"get"
        }).done(function(data){

            _this.setState({newsData:data})
                console.log(_this.state.newsData)

        })

    }
    render(){
        const _this = this
        var newdateArr = ""
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

            _this.setState({newsData:data})
            newdateArr = data

        })

        return(
            <div className="newsImg">
                {newdateArr.map((index,value)=>{

                    console.log(index)
                })}
            </div>
        )
    }
}


